* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #fff;
  color: #000;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
  min-height: 100vh;
  max-width: 90rem;
  padding: 0 6.25rem;
  margin: 0 auto;
  width: 100%;
  display: flex;
  flex-direction: column;
}